  display: none;
}

*, *::before, *::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%; /*1 rem = 10px */
}

body {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 1.6rem; /*16px*/
  line-height: 1.6;
  color: #fff;
  background-color: #0E0E12;
}
body.block-body {
  overflow: hidden;
}

h1, h2, h3, h4, h5 {
  font-family: "Teko", sans-serif;
  font-weight: 600;
  color: #C3AA72;
  line-height: 1.2;
}

h1, h2, h3, h4, h5, p {
  margin: 0;
}

h1 {
  font-size: clamp(3.6rem, 1rem + 3.8vw, 6.4rem);
  font-weight: 700;
}

h2 {
  font-size: clamp(3.2rem, 1rem + 3.5vw, 4.8rem);
}

h3 {
  font-size: clamp(2.6rem, 1rem + 2.4vw, 3.2rem);
}

h4 {
  font-size: clamp(2rem, 1rem + 1.9vw, 2.8rem);
  line-height: 1.3;
}

h5 {
  font-size: clamp(1.8rem, 1rem + 1.4vw, 2.4rem);
  line-height: 1.4;
}

p {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: clamp(1.6rem, 1rem + 1vw, 1.78rem);
}

b {
  font-weight: 700;
}

img,
picture {
  width: 100%;
  display: block;
}

button {
  border: none;
  margin: 0;
  padding: 0;
  width: auto;
  overflow: visible;
  background: transparent;
}

a {
  text-decoration: none;
  text-underline-offset: 4px;
}

.alert-danger {
  border-left: 4px solid #721c24;
}

.alert-success {
  border-left: 4px solid #155724;
}

.alert-warning {
  border-left: 4px solid #856404;
}

.my-container {
  margin: 0 auto;
  width: min(100%, 140rem);
}

.container-fluid {
  padding: 0 !important;
}

.row {
  margin: 0 auto;
}

.show {
  display: block;
}

.hidden {
  display: none;
}

.my-paddings {
  padding: 1.6rem;
}
@media (min-width: 768px) {
  .my-paddings {
    padding: 1.8rem 3.2rem;
  }
}
@media (min-width: 992px) {
  .my-paddings {
    padding: 3.2rem 8rem;
  }
}

.btn-gold {
  display: inline-block;
  background-color: #C3AA72;
  color: #0E0E12;
  font-weight: 600;
  text-decoration: none;
  padding: 1.6rem 3.2rem;
  text-align: center;
  border-radius: 0.4rem;
  border: none;
  transition: background-color 0.2s ease-in-out 0s;
}
.btn-gold:hover {
  background-color: rgb(181.1641791045, 150.3992537313, 81.4858208955);
  color: #0E0E12;
  cursor: pointer;
}
.btn-gold:active {
  background-color: rgb(162.5708955224, 133.7462686567, 69.1791044776);
  color: #0E0E12;
}

.btn-gold-block {
  display: block;
  background-color: #C3AA72;
  color: #0E0E12;
  font-weight: 600;
  text-decoration: none;
  padding: 1.6rem 3.2rem;
  text-align: center;
  border-radius: 0.4rem;
  border: none;
  transition: background-color 0.2s ease-in-out 0s;
}
.btn-gold-block:hover {
  background-color: rgb(181.1641791045, 150.3992537313, 81.4858208955);
  color: #0E0E12;
  cursor: pointer;
}
.btn-gold-block:active {
  background-color: rgb(162.5708955224, 133.7462686567, 69.1791044776);
  color: #0E0E12;
}

.btn-turquoise {
  display: inline-block;
  background-color: #B6EFFB;
  color: #055160;
  font-weight: 600;
  text-decoration: none;
  padding: 1.6rem 3.2rem;
  text-align: center;
  border-radius: 0.4rem;
  border: none;
  transition: background-color 0.2s ease-in-out 0s;
}
.btn-turquoise:hover {
  background-color: rgb(120.424025974, 225.5038961039, 247.625974026);
  color: #055160;
  cursor: pointer;
}
.btn-turquoise:active {
  background-color: rgb(79.3733766234, 216.5064935065, 245.3766233766);
  color: #055160;
}

.btn-red {
  display: inline-block;
  background-color: #d13232;
  color: #fff;
  font-weight: 600;
  text-decoration: none;
  padding: 1.6rem 3.2rem;
  text-align: center;
  border-radius: 0.4rem;
  border: none;
  transition: background-color 0.2s ease-in-out 0s;
}
.btn-red:hover {
  background-color: rgb(179.8037848606, 40.3462151394, 40.3462151394);
  color: #fff;
  cursor: pointer;
}
.btn-red:active {
  background-color: rgb(158.6503984064, 35.5996015936, 35.5996015936);
  color: #fff;
}

.btn-blue {
  display: inline-block;
  background-color: #002147;
  color: #fff;
  font-weight: 600;
  text-decoration: none;
  padding: 1.6rem 3.2rem;
  text-align: center;
  border-radius: 0.4rem;
  border: none;
  transition: background-color 0.2s ease-in-out 0s;
}
.btn-blue:hover {
  background-color: rgb(0, 28.05, 60.35);
  color: #fff;
  cursor: pointer;
}
.btn-blue:active {
  background-color: rgb(0, 24.75, 53.25);
  color: #fff;
}

.btn-back {
  color: #C3AA72;
  font-size: 2.6rem;
  display: flex;
  gap: 0.6rem;
  align-items: center;
  transition: color 0.25s ease-in;
  text-decoration: underline;
  font-family: "Teko", sans-serif;
}
.btn-back:hover {
  color: #fff;
}

.banner-bg-overlay {
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.interna h1,
.interna-details h1 {
  margin: 12rem 0 4.8rem 0;
}

.to-top__button {
  background-color: #C3AA72;
  padding: 0.8rem;
  border-radius: 50%;
  position: fixed;
  bottom: 2.4rem;
  right: 2.4rem;
  opacity: 0;
  z-index: -1;
}
.to-top__button.show-button {
  opacity: 1;
  transition: opacity 0.5s ease-in;
  z-index: 15;
}

.light-mode__button {
  background-color: #3E4347;
  border: 1px solid #3E4347;
  padding: 0.8rem;
  border-radius: 50%;
  position: fixed;
  bottom: 7.5rem;
  right: 2.4rem;
  z-index: 15;
}
.light-mode__button img {
  filter: invert(1);
}

.tabs-listing {
  border-bottom-width: 3px;
}
.tabs-listing .nav-tabs__item {
  margin: 0 2.4rem 0 0;
}
.tabs-listing .nav-tabs__item:last-child {
  margin-right: 0;
}

.nav-tabs__item .nav-link {
  position: relative;
  color: #fff;
  font-weight: 600;
  padding: 1.2rem 1.6rem;
  border: none;
  font-size: 2rem;
}
.nav-tabs__item .nav-link::before {
  content: "";
  width: 0%;
  height: 0.3rem;
  position: absolute;
  bottom: -2px;
  left: 0;
  background-color: #C3AA72;
  z-index: 1;
  transition: all 0.2s ease-in-out 0s;
}
.nav-tabs__item .nav-link.active {
  color: #C3AA72;
  background-color: #0E0E12;
}
.nav-tabs__item .nav-link.active::before {
  width: 100%;
}
.nav-tabs__item .nav-link:hover::before {
  width: 100%;
}

.light-mode {
  background-color: #fff;
  transition: background-color 0.15s ease-in;
}
.light-mode h1, .light-mode h2, .light-mode h3, .light-mode h4, .light-mode h5 {
  color: #002147;
}
.light-mode p {
  color: #0E0E12;
}
.light-mode .blog__content .blog__title:hover {
  color: #3E4347;
}
.light-mode .entrada .entrada__description ul {
  color: #0E0E12;
}
.light-mode .velada__card {
  box-shadow: 0 0 10px rgba(37, 37, 37, 0.6);
}
.light-mode .formulario-group label {
  color: #0E0E12;
}
.light-mode .formulario-group .restablecer-contrasenia,
.light-mode .formulario-group .iniciar-sesion {
  color: #002147;
}
.light-mode .formulario-group .label-imagen__cartelera--titulo {
  color: #0E0E12;
}
.light-mode .velada-form__group--combates #contador-combates {
  color: #055160;
  border-bottom: 2px solid #055160;
}
.light-mode .nav-tabs__item .nav-link {
  position: relative;
  color: #0E0E12;
  font-weight: 600;
  padding: 1.2rem 1.6rem;
  border: none;
  font-size: 2rem;
}
.light-mode .nav-tabs__item .nav-link::before {
  content: "";
  width: 0%;
  height: 0.3rem;
  position: absolute;
  bottom: -2px;
  left: 0;
  background-color: #C3AA72;
  z-index: 1;
  transition: all 0.2s ease-in-out 0s;
}
.light-mode .nav-tabs__item .nav-link.active {
  color: #C3AA72;
  background-color: #3E4347;
}
.light-mode .nav-tabs__item .nav-link.active::before {
  width: 100%;
}
.light-mode .nav-tabs__item .nav-link:hover::before {
  width: 100%;
}
.light-mode .btn-back {
  color: #002147;
}
.light-mode .btn-back:hover {
  color: #3E4347;
}
.light-mode .lista-layout .lista-body__item {
  color: #0E0E12;
}
.light-mode .lista-layout .btn-editar,
.light-mode .lista-layout .btn-eliminar {
  color: #3E4347;
}
.light-mode .lista-layout .btn-eliminar input {
  color: #3E4347;
}

/*LAYOUT*/
/* Si el navegador no soporta .webp*/
.no-webp .home-bg {
  background-image: url(../img/bg-inicio.jpg);
}

.webp .home-bg {
  background-image: url(../img/bg-inicio.webp);
}

.no-webp .contact-bg {
  background-image: url(../img/bg-contacto.jpg);
}

.webp .contact-bg {
  background-image: url(../img/bg-contacto.webp);
}

.header {
  background-color: #3E4347;
  position: relative;
}
.header.home-bg, .header.contact-bg {
  background-position: center center;
  background-size: cover;
  min-height: 100vh;
}
@media (min-width: 768px) {
  .header.home-bg .header__content, .header.contact-bg .header__content {
    min-height: 70rem;
  }
}
.header h1, .header p {
  color: #fff;
}

.logo__header {
  z-index: 10 !important;
}
.logo__header .logo__image {
  width: 12rem;
}
@media (min-width: 768px) {
  .logo__header .logo__image {
    width: 14rem;
  }
}

.header__content {
  z-index: 10 !important;
}
.header__content .header__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  text-align: center;
}
.header__content .header__text h1,
.header__content .header__text p {
  max-width: 80rem;
}

.footer {
  background-color: #3E4347;
}
.footer .logo__footer {
  width: 12rem;
}

.footer__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1.6rem;
}
@media (min-width: 768px) {
  .footer__content {
    flex-direction: row;
    justify-content: space-between;
  }
}

.footer__links .nav__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 2.4rem;
}
.footer__links .nav__item:hover .nav__link {
  color: #C3AA72;
  transition: color 0.3s ease-in;
}
.footer__links .nav__item:hover .nav__link::after {
  width: 0 !important;
}

.footer__line {
  display: block;
  width: 100%;
  background-color: #C3AA72;
  height: 0.1rem;
  margin: 1.6rem 0;
}

.footer__copyright {
  margin: 1.2rem 0;
  color: #fff !important;
}
.footer__copyright a {
  text-decoration: underline;
  font-weight: 500;
  transition: color 0.3s ease-in;
  color: white;
}
.footer__copyright a:hover {
  color: #C3AA72;
}

.nav__pages {
  display: none;
}
@media (min-width: 992px) {
  .nav__pages {
    display: block;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__item:hover .nav__link {
  color: #C3AA72;
  transition: color 0.3s ease-in;
}
.nav__item:hover .nav__link::after {
  width: 100%;
  transition: width 0.3s ease-in;
}

.nav__link {
  position: relative;
  color: #fff;
  padding: 1.8rem 0.6rem;
}
.nav__link::after {
  content: "";
  display: block;
  width: 0%;
  height: 0.4rem;
  background-color: #C3AA72;
  position: absolute;
  bottom: 0;
  left: 0;
  border-radius: 0.2rem;
}

.nav__link--active {
  color: #C3AA72;
}
.nav__link--active::after {
  width: 100%;
}

.nav__buttons,
.nav-mobile__buttons {
  z-index: 10 !important;
}
.nav__buttons .button__login,
.nav-mobile__buttons .button__login {
  display: flex;
  gap: 0.8rem;
  align-items: center;
  color: #fff;
  border: 1px solid #fff;
  padding: 0.8rem 1.2rem;
  border-radius: 0.8rem;
}
.nav__buttons .button__login span,
.nav-mobile__buttons .button__login span {
  display: none;
}
@media (min-width: 480px) {
  .nav__buttons .button__login span,
  .nav-mobile__buttons .button__login span {
    display: block;
  }
}
.nav__buttons .button__login:hover,
.nav-mobile__buttons .button__login:hover {
  box-shadow: 0 0 8px rgba(255, 255, 255, 0.6);
  transition: box-shadow 0.3s ease-out;
}
.nav__buttons .button__hamburger,
.nav-mobile__buttons .button__hamburger {
  color: #fff;
  padding: 0.8rem;
}
.nav__buttons .button__hamburger svg,
.nav-mobile__buttons .button__hamburger svg {
  width: 3.6rem;
  height: 3.6rem;
}
.nav__buttons .button__hamburger:hover,
.nav-mobile__buttons .button__hamburger:hover {
  opacity: 0.8;
  transition: opacity 0.3s ease-in;
}
@media (min-width: 992px) {
  .nav__buttons .button__hamburger,
  .nav-mobile__buttons .button__hamburger {
    display: none;
  }
}

.overlay {
  background-color: rgba(6, 6, 6, 0.3);
  width: 100%;
  height: 100vh;
  display: block;
  visibility: hidden;
  opacity: 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  transition: opacity 0.2s ease-in-out 0s;
  cursor: not-allowed;
}
@media (min-width: 992px) {
  .overlay {
    display: none !important;
  }
}

.overlay-active {
  visibility: visible;
  opacity: 1;
}

.nav-mobile {
  width: 85%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%);
  transition: transform 0.15s cubic-bezier(0.46, 0.03, 0.52, 0.96);
  padding: 2.4rem;
  background-color: #3E4347;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 120;
}
@media (min-width: 992px) {
  .nav-mobile {
    display: none !important;
  }
}

.nav-mobile__content {
  max-width: 80%;
}

.nav-mobile__close {
  position: absolute;
  top: 1.6rem;
  right: 1.6rem;
  background-color: #fff;
  border-radius: 0.4rem;
  padding: 0.4rem;
  margin: 0;
  color: #3E4347;
}
.nav-mobile__close:hover {
  opacity: 0.75;
  transition: opacity 0.3s ease-in;
}

.show-nav-mobile {
